
    <template>
      <section class="market-doc-container">
        <h2>Radio 单选框</h2>
<mku-example>
        
        <template slot="source"><element-demo0 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;mku-alert
      title=&quot;万里江山马未鞍，纵横中原驰骋还。踏破东风非吾愿，岂因艰难而不前？这四句出自哪里？&quot;
    &gt;&lt;/mku-alert&gt;
    &lt;br&gt;

    &lt;mku-radio-group v-model=&quot;value&quot;&gt;
      &lt;mku-radio :label=&quot;item.label&quot; v-for=&quot;item in list&quot;&gt;{{item.name}}&lt;/mku-radio&gt;
    &lt;/mku-radio-group&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  data () {
    return {
      value: '',
      list: [
        {label: 'option1', name: 'A 《水调歌头》' },
        {label: 'option2', name: 'B 《纵马吟》' },
        {label: 'option3', name: 'C 《赤壁赋》' },
        {label: 'option4', name: 'D 《登鹳雀楼》' }
      ]
    }
  },
  methods: {

  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>垂直排版</h3>
<p>垂直排版对<code>button</code>类型无效。</p>
<mku-example>
        
        <template slot="source"><element-demo1 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;mku-alert title=&quot;遥望青云千里处，鸿鹄安与燕雀游。作者是谁？&quot;&gt;&lt;/mku-alert&gt;&lt;br&gt;

    &lt;mku-radio-group v-model=&quot;value&quot; mode=&quot;vertical&quot;&gt;
      &lt;mku-radio :label=&quot;item.label&quot; v-for=&quot;item in list&quot;&gt;{{item.name}}&lt;/mku-radio&gt;
    &lt;/mku-radio-group&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  data () {
    return {
      value: '',
      list: [
        {label: 'A', name: 'A 李白' },
        {label: 'B', name: 'B 苏轼' },
        {label: 'C', name: 'C 纳兰容若' },
        {label: 'D', name: 'D 愣锤' }
      ]
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>作为button使用</h3>
<p>支持<code>large</code> <code>default</code> <code>small</code>三种不同的尺寸。</p>
<mku-example>
        
        <template slot="source"><element-demo2 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div class=&quot;doc-radio-button_wrap&quot;&gt;
    &lt;mku-radio-group type=&quot;button&quot; size=&quot;large&quot; v-model=&quot;value1&quot;&gt;
      &lt;mku-radio label=&quot;option1&quot;&gt;Vue&lt;/mku-radio&gt;
      &lt;mku-radio label=&quot;option2&quot;&gt;React&lt;/mku-radio&gt;
      &lt;mku-radio label=&quot;option3&quot;&gt;Angular&lt;/mku-radio&gt;
      &lt;mku-radio label=&quot;option4&quot;&gt;Jquery&lt;/mku-radio&gt;
    &lt;/mku-radio-group&gt;

    &lt;mku-radio-group type=&quot;button&quot; v-model=&quot;value2&quot;&gt;
      &lt;mku-radio label=&quot;option1&quot;&gt;Html&lt;/mku-radio&gt;
      &lt;mku-radio label=&quot;option2&quot;&gt;Css&lt;/mku-radio&gt;
      &lt;mku-radio label=&quot;option3&quot;&gt;Javascript&lt;/mku-radio&gt;
      &lt;mku-radio label=&quot;option4&quot;&gt;TypeScript&lt;/mku-radio&gt;
    &lt;/mku-radio-group&gt;

    &lt;mku-radio-group
      type=&quot;button&quot;
      size=&quot;small&quot;
      button-type=&quot;fill&quot;
      v-model=&quot;value3&quot;
    &gt;
      &lt;mku-radio label=&quot;option1&quot;&gt;工程化&lt;/mku-radio&gt;
      &lt;mku-radio label=&quot;option2&quot;&gt;组件化&lt;/mku-radio&gt;
      &lt;mku-radio label=&quot;option3&quot;&gt;可视化&lt;/mku-radio&gt;
      &lt;mku-radio label=&quot;option4&quot;&gt;基础建设&lt;/mku-radio&gt;
    &lt;/mku-radio-group&gt;
  &lt;/div&gt;
&lt;/template&gt;
&lt;script&gt;
export default {
  data () {
    return {
      value1: 'option1',
      value2: 'option2',
      value3: 'option3'
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>禁用</h3>
<mku-example>
        
        <template slot="source"><element-demo3 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div class=&quot;doc-radio-button_wrap&quot;&gt;
    &lt;mku-radio-group&gt;
      &lt;mku-radio label=&quot;option1&quot;&gt;葫芦娃&lt;/mku-radio&gt;
      &lt;mku-radio label=&quot;option2&quot; disabled&gt;三眼童子&lt;/mku-radio&gt;
      &lt;mku-radio label=&quot;option3&quot;&gt;喜洋洋&lt;/mku-radio&gt;
      &lt;mku-radio label=&quot;option4&quot;&gt;猫和老鼠&lt;/mku-radio&gt;
    &lt;/mku-radio-group&gt;

    &lt;mku-radio-group type=&quot;button&quot;&gt;
      &lt;mku-radio label=&quot;option1&quot;&gt;英雄联盟&lt;/mku-radio&gt;
      &lt;mku-radio label=&quot;option2&quot; disabled&gt;已被禁用&lt;/mku-radio&gt;
      &lt;mku-radio label=&quot;option3&quot;&gt;绝地求生&lt;/mku-radio&gt;
      &lt;mku-radio label=&quot;option4&quot;&gt;地下城与勇士&lt;/mku-radio&gt;
    &lt;/mku-radio-group&gt;
  &lt;/div&gt;
&lt;/template&gt;
</code></pre></template></mku-example><h3>Radio Attributes</h3>
<table>
<thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">可选值</th>
<th style="text-align:left">默认值</th>
<th style="text-align:left"></th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">value</td>
<td style="text-align:left">radio绑定的值，可用<code>v-model</code>进行双向数据绑定。仅单独使用时有效</td>
<td style="text-align:left"><code>String | Number</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">label</td>
<td style="text-align:left">指定当前radio项的<code>value</code>值，仅组合使用时有效</td>
<td style="text-align:left"><code>String | Number</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">disabled</td>
<td style="text-align:left">是否禁用当前radio项</td>
<td style="text-align:left"><code>Boolean</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>false</code></td>
<td style="text-align:left">否</td>
</tr>
</tbody>
</table>
<h3>Radio Events</h3>
<table>
<thead>
<tr>
<th style="text-align:left">事件名</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">回调函数参数</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">change</td>
<td style="text-align:left">点击radio值发生变化时触发，仅在单独使用时有效</td>
<td style="text-align:left">radio的label值</td>
</tr>
</tbody>
</table>
<h3>Radio-Group Attributes</h3>
<table>
<thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">可选值</th>
<th style="text-align:left">默认值</th>
<th style="text-align:left"></th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">value</td>
<td style="text-align:left">radio-group绑定的值，可用<code>v-model</code>进行双向数据绑定</td>
<td style="text-align:left"><code>String | Number</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">mode</td>
<td style="text-align:left">指定radio是水平还是垂直排列，对button组合时无效</td>
<td style="text-align:left"><code>String</code></td>
<td style="text-align:left"><code>horizontal | vertical</code></td>
<td style="text-align:left"><code>horizontal</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">type</td>
<td style="text-align:left">指定是作为<code>radio-group</code>还是<code>button-group</code>使用</td>
<td style="text-align:left"><code>String</code></td>
<td style="text-align:left"><code>radio | button</code></td>
<td style="text-align:left"><code>radio</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">size</td>
<td style="text-align:left">指定<code>radio-group</code>组合使用时的尺寸</td>
<td style="text-align:left"><code>String</code></td>
<td style="text-align:left"><code>large | default | small</code></td>
<td style="text-align:left"><code>default</code></td>
<td style="text-align:left">否</td>
</tr>
</tbody>
</table>
<h3>Radio-Group Events</h3>
<table>
<thead>
<tr>
<th style="text-align:left">事件名</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">回调函数参数</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">change</td>
<td style="text-align:left">在点击选中项发生变化时触发</td>
<td style="text-align:left">返回选中项的label值</td>
</tr>
</tbody>
</table>

      </section>
